import React from "react"
// import { useStaticQuery, Link, graphql } from "gatsby"
import { Link } from "gatsby"


const ListLink = props => (
    <li style={{ display: `inline-block`, marginRight: `3rem` }}>
      <Link to={props.to}>{props.children}</Link>
    </li>
  )

export default ({ children }) => {

  return (
  <div style={{ margin: `1.5rem auto`, marginBottom:'3rem', maxWidth: 768, padding: `0 1rem` }}>
       <header style={{ marginBottom: `3rem` }}>

            <ListLink to="/t3f19/"></ListLink>
            <ListLink to="/t3f19/exercises/">Exercises</ListLink>
            <ListLink to="/t3f19/projects/">Projects</ListLink>
            <ListLink to="/t3f19/resources/">Resources</ListLink>
            <ul style={{ listStyle: `none`, float: `right`, padding: `2px 1rem`, marginBottom: `calc(1.08rem / 2)` }}>
              <a href="https://paper.dropbox.com/doc/19F-Typography-III--AkKFeTa8RYmiZ1G1vt2iBpDVAQ-nqSOiC6hnQAqZH4TNBoSq" target="_blank">Syllabus</a>
            </ul>
            <ul style={{ listStyle: `none`, float: `right`, padding: `2px 1rem`, marginBottom: `calc(1.08rem / 2)` }}>
              <a href="https://paper.dropbox.com/doc/19F-T3-Class-notes--AkrJixpGZI_eUAo5sFDOnXKHAQ-PFKwwPKL83407aCn2zd2q" target="_blank" rel="noopener noreferrer">In-class note</a>
            </ul>
        </header>
      {children}
    </div>
  )
}
